.popular-destinations{
    margin-bottom: 20px;
    text-align: center;

    @media (min-width: $desktop_width) {
        margin-bottom: 80px;
        width: 1440px;
    }
}

.popular-destinations__title{
    margin: 0;
    margin-bottom: 20px;
    @include title-text;
    color: $basic_red;

    @media (min-width: $desktop_width){
        margin-bottom: 40px;
        @include title-text-desktop;
    }
}

.popular-destinations__slider{
    height: 210px;
    margin-bottom: 20px;
    padding: 0 15px;
    position: relative;

    @media (min-width: $desktop_width){
        height: 800px;
        margin-bottom: 84px;
        padding: 0;
    }
}

.popular-destinations__list{
    @include no-list;

    @media (min-width: $desktop_width){
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        gap: 60px;
        margin-left: -550px;
    }
}

.popular-destinations__item{
    position: absolute;
    z-index: 1;

    @media (min-width: $desktop_width){
        position: relative;
        flex-basis: 800px;
        flex-shrink: 0;
        height: 800px;
        
    }
}

.popular-destinations__item--active{
    z-index: 2;

    @media (min-width: $desktop_width){
        z-index: 1;
    }
}

.popular-destinations__img{
    border-radius: 16px;
}

.popular-destinations__text{
    margin: 0;
    position: absolute;
    top: 8px;
    left: 15px;
    color: $basic_white;
    font-family: "Red Hat Text";
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.popular-destinations__slider-list{
    @include no-list;
    position: absolute;
    top: 90px;
    width: 92.5%;
    z-index: 4;
    display: flex;
    justify-content: space-between;

    @media (min-width: $desktop_width){
        display: none;
    }
}

.popular-destinations__slider-arrow{
    border: none;
    outline: none;
    background: none;
    opacity: 0.5;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active{
        opacity: 1;
    }
}

.popular-destinations__slider-controls{
    @include no-list;
    display: flex;
    justify-content: center;
    gap: 6px;
    position: absolute;
    left: 177px;
    bottom: 12px;
    z-index: 3;

    @media (min-width: $desktop_width) {
        display: none;
    }
}

.popular-destinations__button{
    padding: 0;
    width: 8px;
    height: 8px;
    border: none;
    border-radius: 50%;
    background-color: rgba($basic_red, 0.5);
    cursor: pointer;

    &--active{
        background-color: $basic_red;
    }
}

.popular-destinations__link{
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    gap: 6px;

    @media (min-width: $desktop_width){
        gap: 10px;
        padding: 9.5px 31px;

        & svg{
            width: 40px;
            height: 40px;
        }
    }
}