.promo{
    margin-bottom: 20px;
    padding: 142px 15px 60px;
    background: linear-gradient(180deg, #0D606F 0%, rgba(13, 96, 111, 0.00) 25.1%), url(../../img/header-bg-mobile.jpg) center center no-repeat;

    @media (min-width: $desktop_width){
        margin-bottom: 80px;
        padding: 122px 428px 254px 120px;
        background: url("../../img/header-bg-desktop.jpg") top center no-repeat;
    }
}

.promo__slogan{
    display: none;

    @media (min-width: $desktop_width){
        display: block;
        margin: 0;
        margin-bottom: 50px;
        color: $basic_white;
        font-family: Aleo;
        font-size: 80px;
        font-style: normal;
        font-weight: 700;
        line-height: 96px;
    }
}

.promo__subscribe{
    position: relative;

    & label{
        display: none;

        @media (min-width: $desktop_width){
            display: block;
            margin-bottom: 46px;
            color: $basic_white;
            font-family: Aleo;
            font-size: 20px;
            font-style: italic;
            font-weight: 700;
            line-height: 24px;
        }
    }

    & input{
        outline: none;
        width: 100%;
        padding: 15px 101px 15px 30px;
        border: 1px solid rgba($basic_white, 0);
        border-radius: 16px;
        background-color: rgba($basic_white, 0.7);

        color: $basic_red;
        font-family: Aleo;
        font-size: 14px;
        font-style: italic;
        font-weight: 400;
        line-height: normal;

        @media (min-width: $desktop_width){
            padding: 31px 291px 31px 41px;
            border: 2px solid rgba($basic_white, 0);

            font-family: Aleo;
            font-size: 24px;
            line-height: 29px;
        }

        &::-webkit-search-decoration,
        &::-webkit-search-cancel-button,
        &::-webkit-search-results-button,
        &::-webkit-search-results-decoration{
            -webkit-appearance:none;
        }

        &::placeholder{
            color: rgba($basic_red, 0.9);
        }

        &:focus{
            border: 1px solid $basic_red;
            color: $basic_red;

            @media (min-width: $desktop_width){
                border: 2px solid $basic_red;
            }
        }

    }
}

.promo__button{
    position: absolute;
    bottom: 9px;
    right: 9px;

    @media (min-width: $desktop_width){
        right: 18px;
        bottom: 18px;
    }
}