.stories{
    margin-bottom: 0;
}

.stories__title{
    margin: 0;
    margin-bottom: 10px;
    @include title-text;
    color: $basic_red;

    @media (min-width: $desktop_width){
        @include title-text-desktop;
        margin-bottom: 40px;
    }
}

.stories__wrapper{
    padding: 10px 15px 78px;
    background:  linear-gradient(to bottom, rgba(255, 255, 255, 0.70) 0%, rgba(255, 255, 255, 0.70) 91%, rgba(255, 255, 255, 0) 91%, rgba(255, 255, 255, 0) 100%), url(../../img/footer-bg-mobile.jpg) top left/cover no-repeat;
    text-align: center;

    @media (min-width: $desktop_width) {
        padding: 40px 120px 102px;
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0.70) 0%, rgba(255, 255, 255, 0.70) 94.5%, rgba(255, 255, 255, 0) 94.5%, rgba(255, 255, 255, 0) 100%), url("../../img/footer-bg-desktop.jpg") top left/cover no-repeat;
    }
}

.stories__list{
    @include no-list;
}

.stories__item{
    display: grid;
    margin-bottom: 15px;
    grid-template-areas:
    "img title"
    "img text";
    grid-template-columns: 155px 1fr;
    gap: 10px;
    text-align: left;

    @media (min-width: $desktop_width){
        margin-bottom: 30px;
        grid-template-columns: 400px 1fr;
        gap: 24px 40px;
    }
}

.stories__item-title{
    grid-area: title;
    margin: 0;
    color: $basic_green;
    font-family: Aleo;
    font-size: 11px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    @media (min-width: $desktop_width){
        font-size: 35px;
        font-style: normal;
        font-weight: 400;
        line-height: 42px;
    }
}

.stories__item-text{
    grid-area: text;
    margin: 0;
    font-family: Aleo;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @media (min-width: $desktop_width){
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 26px;
    }
}

.stories__item-link{
    color: $basic_green;
    font-weight: 700;
    line-height: normal;
    text-decoration-line: underline;
}

.stories__item-link:hover,
.stories__item-link:focus{
    opacity: 0.8;
}
    

.stories__item-img{
    grid-area: img;
    align-self: center;
    border-radius: 16px;
    box-shadow: 0px 4px 4px 0px #0D606F;
    width: 155px;
    height: 130px;

    @media (min-width: $desktop_width){
        width: 400px;
        height: 250px;
    }
}

.stories__link{
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    gap: 6px;

    @media (min-width: $desktop_width){
        gap: 10px;
        padding: 9.5px 31px;

        & svg{
            width: 40px;
            height: 40px;
        }
    }
}