.trip-steps{
    margin-bottom: 20px;
    padding: 0 15px;

    @media (min-width: $desktop_width) {
        margin-bottom: 95px;
        padding: 0 131px;
    }
}

.trip-steps__title{
    margin: 0;
    margin-bottom: 20px;
    @include title-text;
    color: $basic_red;

    @media (min-width: $desktop_width){
        margin-bottom: 40px;
        @include title-text-desktop;
    }
}

.trip-steps__list{
    @include no-list;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;

    @media (min-width: $desktop_width){
        display: flex;
        justify-content: space-between;
        gap: 70px;
    }
}

.trip-steps__item{
    
    &:first-child{
        text-align: left;

        @media (min-width: $desktop_width){
            text-align: center;
        }
    }

    &:nth-child(2){
        text-align: center;

        @media (min-width: $desktop_width){
            text-align: center;
        }
    }

    &:last-child{
        text-align: right;

        @media (min-width: $desktop_width){
            text-align: center;
        }
    }

    & svg{
        width: 70px;
        height: 70px;

        @media (min-width: $desktop_width){
            width: 100px;
            height: 100px;
        }
    }
}

.trip-steps__text{
    margin: 0;
    margin-top: 20px;
    color: $basic_green;
    font-family: Aleo;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @media (min-width: $desktop_width) {
        margin-top: 30px;
        font-size: 20px;
        line-height: 24px;

        & br{
            display: none;
        }
    }
}