@mixin title-text {
    font: {
        family: "Red Hat Text";
        size: 14px;
        style: normal;
        weight: 500;
        height: normal;
    }
    text: {
        transform: uppercase;
        align: center;
    }
}

@mixin title-text-desktop {
    font: {
        family: "Red Hat Text";
        size: 40px;
        style: normal;
        weight: 500;
        height: normal;
    }
    text: {
        transform: uppercase;
        align: center;
    }
}

@mixin no-list{
    margin: 0;
    padding: 0;
    list-style: none;
}