.header-nav{
    position: relative;
    font-family: "Red Hat Text";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;

    @media (min-width: $desktop_width){

    }
}

.header-nav__toggle{
    border: none;
    outline: none;
    background: none;
    width: 25px;
    height: 25px;
    cursor: pointer;

    @media (min-width: $desktop_width) {
        display: none;
    }
}

.header-nav--closed .header-nav__toggle::before{
    content: ' ';
    position: absolute;
    top: 5px;
    right: 0;
    width: 26px;
    height: 2.6px;
    background-color: $basic_white;
    box-shadow: 0 7px $basic_white, 0 14px $basic_white;
    cursor: pointer;
}

.header-nav--opened .header-nav__toggle{
    position: absolute;
    top: 10px;
    right: 135px;
    outline: none;
    background: none;
    width: 25px;
    height: 25px;
    cursor: pointer;
}

.header-nav--opened .header-nav__toggle::before{
    content: ' ';
    position: absolute;
    top: 10px;
    right: 0;
    width: 15px;
    height: 2px;
    background-color: rgba($basic_white, 0.7);
    transform: rotate(45deg);
    cursor: pointer;
    z-index: 3;
}
    
.header-nav--opened .header-nav__toggle::after{
    content: ' ';
    position: absolute;
    top: 10px;
    right: 0;
    width: 15px;
    height: 2px;
    background-color: rgba($basic_white, 0.7);
    transform: rotate(-45deg);
    cursor: pointer;
    z-index: 3;
}

.header-nav__list{
    margin: 0;
    padding: 44px 15px 30px 44px;
    list-style: none;
    position: absolute;
    top: 0;
    right: 0;
    width: 165px;
    box-shadow: 0px 4px 3px 0px rgba($basic_green, 0.25);
    border-radius: 10px;
    background-color: $basic_red;
    z-index: 2;

    @media (min-width: $desktop_width){
        position: static;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        gap: 42px;
        box-shadow: none;
        background: none;
        border: none;
    }
}

.header-nav--closed .header-nav__list{
    display: none;

    @media (min-width: $desktop_width){
        display: flex;
    }
}

.header-nav__item{
    margin-bottom: 10px;

    @media (min-width: $desktop_width){
        margin: 0;
    }
}

.header-nav__link{
    color: $basic_white;
    font-family: "Red Hat Text";
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;

    &:hover,
    &:focus{
        opacity: 0.8;
    }
}

.header-nav__link--additional{
    @media (min-width: $desktop_width){
        display: none;
    }
}