.header{
    position: absolute;
    top: 45px;
    left: 0;
    right: 0;

    @media (min-width: $desktop_width) {
        top: 0;
        height: 90px;
        background: rgba(242, 120, 92, 0.70);
        box-shadow: 0px 4px 4px 0px rgba(13, 96, 111, 0.50);
    }
}

.header__wrapper{
    padding: 0 20px 0 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $desktop_width){
        padding: 25px 120px 24px;
    }
}

.header__logo:hover,
.header_logo:focus{
    opacity: 0.8;
}

.header_logo:active{
    opacity: 0.6;
}

@media (min-width: $desktop_width){
    .header__logo-image--mobile{
        display: none;
    }
}

.header__logo-image--desktop{
    display: none;

    @media (min-width: $desktop_width){
        display: inline;
    }
}

.header__button{
    display: none;

    @media (min-width: $desktop_width){
        display: block;

        & button{
            cursor: pointer;
            padding: 10px 30px;
            font-family: "Red Hat Text";
            font-size: 16px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
        }
    }
}