.button{
    padding: 7.5px 9px;
    border: none;
    border-radius: 10px;
    background-color: $basic_green;
    box-shadow: 0px 2px 3px 0px rgba(13, 96, 111, 0.16), 0px 0px 3px 0px rgba(13, 96, 111, 0.08);

    color: $basic_white;
    font-family: "Red Hat Text";
    font-size: 11px;
    font-style: normal;
    font-weight: 500;
    line-height: 15px;

    &:focus{
        opacity: 0.7;
    }

    @media (min-width: $desktop_width) {
        border: none;
        padding: 13.5px 32.5px;
        
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 32px;
    }

}