.footer{
    width: 100%;
    position: absolute;
    bottom: 10px;
    // left: 0;
    // right: 0;

    @media (min-width: $desktop_width){
        height: 60px;
        bottom: 0;
        padding: 13px 120px 13px 590px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.footer__author{
    display: none;

    @media (min-width: $desktop_width){
        display: block;
        margin: 0;
        color: $basic_white;
        font-family: "Red Hat Text";
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 21px;
    }
}

.footer__social-buttons{
    @include no-list;
    display: flex;
    justify-content: center;
    gap: 20px;
}

.footer__link:hover,
.footer__link:focus{
    opacity: 0.8;
}

.footer__link:active{
    opacity: 0.6;
}